.zolo-author-wrap {
    --zolo-border-color: #e9edf4;
    --zolo-bg-color: #fff;
    --zolo-font-color: #6d727d;
    --zolo-hover-color: #ef233c;
    --zolo-description-color: #8d99ae;
    --zolo-title-color: #2b2d42;
    --zolo-post-border-color: #d8dbe0;
    display: grid;
    gap: 30px;

    &.zolo-grid {
        grid-template-columns: repeat(2, 1fr);
    }
    .zolo-item {
        position: relative;
        border-radius: 10px;
        transition: all 0.3s ease;
        border: 1px solid var(--zolo-border-color);
        background-color: var(--zolo-bg-color);
    }

    .zolo-post-count {
        position: absolute;
        top: 0;
        right: 0;
        font-size: 12px;
        color: var(--zolo-font-color);
        text-transform: capitalize;
        line-height: 1;
        padding: 6px 8px;
        border-radius: 6px;
        border: 1px solid var(--zolo-post-border-color);
    }

    .zolo-content {
        position: relative;
        z-index: 1;
        width: 100%;
        box-sizing: border-box;
    }

    .zolo-name {
        margin: 0 0 2px;

        a {
            display: inline-flex;
            font-size: 18px;
            font-weight: 600;
            color: var(--zolo-title-color);
            line-height: 1;
            text-transform: capitalize;
            transition: all 0.3s ease;
            text-decoration: none;

            &:hover {
                color: var(--zolo-hover-color);
            }
        }
    }

    .zolo-description {
        font-size: 15px;
        color: var(--zolo-description-color);
        padding-top: 10px;
    }

    .zolo-role {
        font-size: 15px;
        color: var(--zolo-description-color);
    }

    .zolo-link {
        display: inline-flex;
        align-items: center;
        margin-top: 15px;
        gap: 10px;

        a {
            color: var(--zolo-description-color);
            transition: all 0.3s ease;

            svg {
                width: 18px;
                height: 18px;
                display: block;
            }

            &:hover {
                color: var(--zolo-hover-color);
            }
        }
    }

    &.zolo-grid {
        .zolo-post-count {
            margin: clamp(15px, 2.5vw, 30px);
        }

        .zolo-content {
            text-align: center;
        }

        .zolo-link {
            justify-content: center;
        }

        .zolo-item {
            padding: clamp(15px, 2.5vw, 30px);
        }

        .zolo-image {
            margin: 35px 30px 20px 30px;
            text-align: center;

            a img {
                width: 150px;
                height: 150px;
                max-width: unset;
                border-radius: 100%;
                object-fit: cover;
            }
        }
    }
}

@media (min-width: 768px) {
    .zolo-author-wrap {
        &.zolo-grid {
            grid-template-columns: repeat(3, 1fr);
        }
    }
}

@media (min-width: 1024px) {
    .zolo-author-wrap {
        &.zolo-grid {
            grid-template-columns: repeat(4, 1fr);
        }
    }
}
